@charset "UTF-8";
/* Template Name: 
   File: Main Css File
*/
@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Spectral:wght@300;400;500;600;700&display=swap");
@tailwind base;
@tailwind components;
@tailwind utilities;
.topbar {
  @apply bg-transparent h-[52px] px-2 py-2 backdrop-blur-md;
}

@media (max-width: 991.98px) {
  .topbar {
    margin-left: 0 !important;
  }

  .brand-logo {
    display: none !important;
  }
}
body[data-sidebar-size=collapsed] .topbar {
  margin-left: 0 !important;
  left: 0;
}

.navbar li button {
  @apply text-gray-400 hover:text-gray-200;
}

.navbar li.active button {
  @apply text-white;
}

.navbar li .dropdown-menu ul li .nav-link {
  @apply text-slate-400 dark:text-slate-300 md:text-slate-700;
}

.navbar li.active .dropdown-menu ul li .nav-link.active {
  @apply text-blue-500 bg-slate-900/30 md:bg-slate-50 dark:bg-slate-800/70;
}

.divide-gray-100 > :not([hidden]) ~ :not([hidden]) {
  border-color: transparent !important;
}

.divide-y > :not([hidden]) ~ :not([hidden]) {
  border-top-width: 0px !important;
  border-bottom-width: 0px !important;
}

#mobile-menu-2 {
  @apply block;
}

#toggle-menu {
  @apply hidden;
}

.navbar-menu {
  @apply fixed;
}

@media (max-width: 991px) {
  #mobile-menu-2 {
    @apply bg-white dark:bg-slate-800 absolute inset-x-0 hidden top-[60px] h-auto max-h-[500px] p-2 overflow-auto z-[1000] shadow-md;
  }

  #toggle-menu {
    @apply block;
  }

  .navbar-menu {
    @apply relative;
  }
}
.NavMenu li a {
  @apply py-2 px-4 block text-sm font-medium  text-slate-600 relative;
}
.NavMenu li a.active label {
  @apply text-primary-500;
}
.NavMenu li a label {
  @apply cursor-pointer appearance-none block relative w-full;
}
.NavMenu li a label::after {
  content: "";
  font-family: "tabler-icons" !important;
  line-height: 1;
  position: absolute;
  right: 0;
  top: 3px;
  font-size: 14px;
}
.NavMenu li ul {
  @apply absolute flex flex-col bg-white min-w-[200px] top-full;
  transition: all 0.2s;
  transform: translateY(10px);
  visibility: hidden;
  opacity: 0;
}
.NavMenu li ul li {
  @apply relative;
}
.NavMenu li ul li a {
  @apply text-slate-600 py-2 px-3 block;
  transition: all 0.5s;
}
.NavMenu li ul li a.active {
  color: theme("colors.primary.500") !important;
  background-color: theme("colors.primary.500/0.04");
}
.NavMenu li ul li a label::after {
  transform: rotate(-90deg);
}
.NavMenu li ul li ul {
  @apply absolute left-full top-0;
}
.NavMenu > li > a label::after {
  right: -18px;
  top: 10px;
}

@media (min-width: 992px) {
  .NavMenu li ul li a:hover {
    @apply bg-slate-50;
  }
  .NavMenu li ul li:hover > ul {
    visibility: visible;
    opacity: 1;
    transform: translateY(0);
  }
  .NavMenu li:hover > ul {
    visibility: visible;
    opacity: 1;
    transform: translateY(0);
  }
}
@media (max-width: 991.98px) {
  .NavMenu {
    @apply w-[100%] flex-1 fixed flex-col bg-white left-0   px-0 justify-start overflow-y-scroll;
  }
  .NavMenu li {
    @apply border-b border-slate-200 dark:border-slate-700;
  }
  .NavMenu li:last-child {
    border: none;
  }
  .NavMenu li ul {
    @apply left-0 top-0;
    position: initial;
    visibility: visible;
    opacity: 1;
    display: none;
  }
  .NavMenu li ul li ul {
    @apply left-0 top-0 bg-slate-100 -mt-2;
    position: initial;
    visibility: visible;
    opacity: 1;
    display: none;
    position: inherit;
  }
  .NavMenu li ul li ul li a {
    @apply text-sm text-slate-600 font-medium py-2 pl-8 pr-4;
    text-transform: initial;
  }
  .NavMenu li ul li input:checked + ul {
    @apply flex flex-col mb-4;
  }
  .NavMenu li a {
    padding: 8px 15px;
    border-bottom: 1px solid #fff;
  }
  .NavMenu li a label::after {
    right: 10px;
  }
  .NavMenu li input:checked + ul {
    display: flex;
    flex-direction: column;
    margin-bottom: 15px;
  }
}
@media (max-width: 768px) {
  .NavMenu {
    width: 100%;
  }
}
/*********************************/
/*             General           */
/*===============================*/
html {
  @apply scroll-smooth;
}

body {
  overflow-x: hidden !important;
  @apply text-base;
  letter-spacing: 0.1px;
}
body[data-layout-mode=dark] .top-icon, body[data-layout-mode=light] .top-icon {
  @apply text-slate-500;
}

h1, h2, h3, h4, h5, h6, .h1, .h2, .h3, .h4, .h5, .h6 {
  @apply font-sans;
}

p {
  @apply leading-relaxed;
}

@media (max-width: 1024px) {
  .page-wrapper {
    min-width: 100% !important;
  }
}
.main-title {
  @apply bg-clip-text text-transparent bg-gradient-to-r from-[#e7f2ef] to-[#e7f2ef] text-[50px] md:text-[90px] lg:text-[100px] font-thin leading-[100px] md:leading-[150px] text-center;
}

.main-title-dark {
  @apply bg-clip-text text-transparent bg-gradient-to-r from-[#0c1818] to-[#111e1e] text-[50px] md:text-[90px] lg:text-[100px] font-thin leading-[100px] md:leading-[150px];
}

.main-solid-title {
  font-weight: 700;
  font-size: 150px;
  line-height: 60px;
  letter-spacing: -2px;
  text-transform: uppercase;
  text-align: center;
  color: #e7f2ef;
  position: relative;
  bottom: -40px;
}

.price-option input ~ .monthly {
  @apply bg-primary-500/10 text-primary-500;
}
.price-option input:checked ~ .monthly {
  @apply bg-white text-gray-700;
}
.price-option input:checked ~ .yearly {
  @apply bg-primary-500/10 text-primary-500;
}

/* Carousel */
@keyframes carouselAnim {
  from {
    transform: translate(0, 0);
  }
  to {
    transform: translate(calc(-100% + (1*400px)));
  }
}
@media only screen and (max-width: 768px) {
  .container .carousel-items {
    animation: carouselAnim 6s infinite alternate linear;
  }

  @keyframes carouselAnim {
    from {
      transform: translate(0, 0);
    }
    to {
      transform: translate(calc(-100% + (1*400px)));
    }
  }
}
.carousel-focus:hover {
  transition: all 0.8s;
  transform: scale(1.1);
}

[role=tab][aria-selected=false] {
  border-color: theme("colors.transparent") !important;
}

[role=tab][aria-selected=true] {
  color: theme("colors.blue.600") !important;
  border-color: theme("colors.blue.600") !important;
}

[role=tab][aria-selected=true]:hover {
  color: theme("colors.blue.600") !important;
  border-color: theme("colors.blue.600") !important;
}

.custom-checkbox .checked,
.custom-checkbox input[type=checkbox] {
  display: none;
}

.custom-checkbox input[type=checkbox]:checked ~ .unchecked {
  display: none;
}

.custom-checkbox input[type=checkbox]:checked ~ .checked {
  display: inline-block;
}

.swiper-button-next,
.swiper-button-prev {
  width: 48px !important;
}
.swiper-button-next:after,
.swiper-button-prev:after {
  font-size: 16px !important;
  color: theme("colors.white");
  background-color: theme("colors.slate.500/0.40");
  width: 30px;
  height: 30px;
  display: block;
  border-radius: 50%;
  text-align: center;
  line-height: 30px !important;
}